
import ListCompent from '../list/listCompent.vue';
import XHeader from 'vux/src/components/x-header/index.vue'

import {API,getQuery} from '../../services'

export default {
components: {
XHeader,
ListCompent
},
data () {
return {
storeName:null,
storeFace:null,
storebgPic:null,
storeId:null,
goodsList:[1,5,7,6,8],
}
},

methods:{
routerBack(){
this.$router.goBack();
},
/* 跳到店铺详情页面 */
goInfo(){
window.location.href="/#/shopInfo/"+this.storeId;
},
/* 数据初始化 */
Initialization(){
this.storeId=this.$route.params.id;
API.main.storeInfo({
storeId:this.storeId
}).then((res)=>{
if(res.body.code==200){
this.storeName=res.body.data.storeName;
this.storeFace=res.body.data.storeFace;
this.storebgPic=res.body.data.storebgPic;
}
});
API.main.storeGoods({
storeId:this.storeId
}).then((res)=>{
if(res.body.code==200){
this.goodsList=res.body.data.shopList.data;
}
});
}
},mounted(){
this.Initialization();
},
activated(){
this.Initialization();
}
}



